#UpdateBox, #UpdatePWBox {
    .personal-info-inputs {
        .input-div {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
    }
    form {
        // position: unset;
        padding: 2.5em;
        width: 90%;
        max-width: 500px;
        *:not(input[type="submit"]) {
            margin:0;
            width: 100%;
            box-sizing: border-box;
            background: transparent;
        }
    }

    .address-div {
        display: grid;
        grid-template-columns: 1fr 5fr;
    }
    .x-btn {
        width: fit-content !important;
        margin-left: auto !important;
        position: absolute;
        top: 1em;
        right: 1em;
        font-size: 1.5em;
    }
}

#UpdatePWBox {
    .input-div {
        display: grid;
    }
    label {
        // height: 0;
    }
}
