#DelBox {
    box-shadow: var(--box-shadow);
    padding: 5em;
    box-sizing: border-box;
    .top, .bottom {
        padding: 1em 1.5em;
    }
    form {
        position: unset;
    }
    .top {
        display: flex;
        justify-content: space-between;
        background: var(--bg-grey);
    }
    input {
        width: 100%;
        margin-bottom: .5em;
        box-sizing: border-box;
        border-color: var(--red);
    }
    input[type="submit"] {
        background: var(--red);
        color: #fff;
    }
    span {
        font-weight: 900;
    }
}

.box {
    width: 350px;
    // height: 200px;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0 !important;
}